import React, { useContext } from 'react'
import Context from '../context'

import MainMenu from './MainMenu';

export default function Breadcrumbs(props) {

    const {setCurrentComponent, breadcrumbs, setBreadcrumbs} = useContext(Context)

    return (
    <div className="breadcrumbs">
        <a 
        href="#" 
        className="btn btn-icon"
        onClick={(e) => {
            e.preventDefault()
            // setCurrentComponent(<MainMenu />)
            setCurrentComponent(breadcrumbs.length ? breadcrumbs[breadcrumbs.length-1] : <MainMenu />)
            setBreadcrumbs(breadcrumbs.slice(0, -1))
        }}><span className="material-icons">arrow_back</span></a>
    </div>
    );

}