import React, { useContext } from 'react'
import Context from '../context';

import Breadcrumbs from './Breadcrumbs';
import NavList from './NavList';
import WordsList from './WordsList';
import ButtonAllWords from './ButtonAllWords';
import Game from './Game';

export default function SubMenu(props) {

    const { setCurrentComponent, answersLength, breadcrumbs, setBreadcrumbs } = useContext(Context)

    return (
    <div className="container list">
        <div className="header">
            <Breadcrumbs />
        </div>
        <div className="list-header">
            <button 
                className="btn btn-long"
                onClick={() => { 
                    setCurrentComponent(<Game start={props.start} end={props.end} questionLength={props.end - props.start + 1} answersLength={answersLength} />)
                    setBreadcrumbs([...breadcrumbs, <SubMenu start={props.start} end={props.end} offset={props.offset} />])
                }}
            >Игра (все {props.end - props.start + 1} слов)</button>
        </div>
        <div className="list-body">
            {/* Sub menu (body) {props.start} - {props.end} */}
            <div>
                { (props.end - props.start) < 20 ? <WordsList start={props.start} end={props.end} /> : <NavList start={props.start} end={props.end} offset={props.offset} /> }
            </div>
        </div>
        <div className="list-footer">
            <ButtonAllWords start={props.start} end={props.end} />
        </div>
    </div>
    );

}