import React, { useState, useEffect, useContext, useMemo, useCallback } from 'react'
// import Context from '../context';

import { randomIntArr, shuffleArray } from '../functions.js'

// import {enRu as dic} from '../data/enRu.js'
import { currentDictionary as dic } from '../currentDictionary.js';

import Breadcrumbs from './Breadcrumbs.js';
import GameQuestion from './GameQuestion';

export default function Learn(props) {

    // const {setCurrentComponent, favorites } = useContext(Context)
    // console.log(props.start, props.end, props.wordsLength)

    const [currentWord, setCurrentWord] = useState(0)
    const [wordsArray, setWordsArray] = useState(randomIntArr(props.start, props.end, props.wordsLength))
    
    // console.log(wordsArray)

    function prevWord(){
        let word = currentWord - 1
        if(word < 0){
            setCurrentWord(wordsArray.length - 1)
        }else{
            setCurrentWord( word )
        }
        
    }
    function nextWord(){
        let word = currentWord + 1
        if(word < wordsArray.length){
            setCurrentWord( word )
        }else{
            setCurrentWord(0)
        }
        
    }

    return (
        <div className="container container-flex game">
            <Breadcrumbs />
            <div className="greedy">
                { wordsArray[currentWord] || wordsArray[currentWord] === 0 ? (
                    
                    <>
                        <div>
                            <div className="counter">{currentWord+1} / {wordsArray.length}</div>
                            <GameQuestion questionID={wordsArray[currentWord]} />

                            <div className="word-ipa">[{dic[wordsArray[currentWord]][2]}]</div>
                            <div className="word-trans">{dic[wordsArray[currentWord]][1]}</div>
                        </div>

                        
                    </>

                ) : ( 

                    <p>Нет слов</p>

                ) }
            </div>
            <div className="btn-wrapper">
                
                { wordsArray[currentWord] || wordsArray[currentWord] === 0 ? (
                    <div className="btn-double">
                        <button 
                            type="button"
                            className="btn btn-long"
                            onClick={() => prevWord()}
                        >Назад</button>
                        <button 
                            type="button"
                            className="btn btn-long btn-dark"
                            onClick={() => nextWord()}
                        >Далее</button>
                    </div>
                ) : "" }
                
            </div>
        </div>
    );

}