import React, { useState, useEffect, useContext, useMemo, useCallback } from 'react'
// import Context from '../context';

import { randomIntArr, shuffleArray } from '../functions.js'

import { currentDictionary as dic } from '../currentDictionary.js';

import Breadcrumbs from './Breadcrumbs.js';
import GameQuestion from './GameQuestion';
import GameResult from './GameResult';

export default function Game(props) {

    // const {setCurrentComponent, favorites } = useContext(Context)
    // console.log(props.start, props.end, props.questionLength)

    const [currentQuiz, setCurrentQuiz] = useState(0)
    const [quizArray, setQuizArray] = useState(randomIntArr(props.start, props.end, props.questionLength))
    const [answersArray, setAnswersArray] = useState(answers(quizArray[currentQuiz]))
    const [wrongAnswers, setWrongAnswers] = useState([])
    const [allowNext, setAllowNext] = useState(false)
    const [success, setSuccess] = useState(false)
    const [fail, setFail] = useState(false)
    const [newGameTrigger, setNewGameTrigger] = useState(false)

    // console.log(answersArray)
    // console.log(quizArray[currentQuiz])
    // console.log(quizArray)

    function btnAnswerHandler(answerID, questionID){
        setAllowNext(true)
        if(quizArray[questionID] === answerID){
            setSuccess(answerID)
        }else{
            setWrongAnswers([...wrongAnswers, quizArray[questionID]])
            setFail(answerID)
        }
    }

    function nextQuiz(){
        setCurrentQuiz( (currentQuiz) => currentQuiz + 1 ) 
        setAnswersArray(answers(quizArray[currentQuiz+1]))
        setAllowNext(false)
        setSuccess(false)
        setFail(false)
    }

    function restart(){
        setCurrentQuiz(0)
        setAllowNext(false)
        setAnswersArray(answers(quizArray[0]))
        setWrongAnswers([])
    }

    function newGame(){
        setQuizArray(randomIntArr(props.start, props.end, props.questionLength))
        restart()
        setNewGameTrigger(!newGameTrigger)
        // console.log(answersArray)
    }

    function answers(answerID, answersLength = props.answersLength){
        // answerID && console.log(answerID, dic[answerID][0])
        // let answersArr = randomIntArr(props.start, props.end, answersLength)
        let answersArr = randomIntArr(0, dic.length-1, answersLength)
        if(!answersArr.includes(answerID)){
            answersArr[0] = answerID
        }
        // console.log(answersArr)
        return shuffleArray(answersArr)
    }

    useEffect(() => {
        setAnswersArray(answers(quizArray[0]))
    }, [newGameTrigger])

    return (
        <div className="container container-flex game">
            <Breadcrumbs />
            <div className="greedy">
                { quizArray[currentQuiz] || quizArray[currentQuiz] === 0 ? (
                    
                    <>
                        <div>
                            <div className="counter">{currentQuiz+1} / {quizArray.length}</div>
                            <GameQuestion questionID={quizArray[currentQuiz]} />
                        </div>
                    
                        <div className="btn-list">
                            { answersArray.map( (item, i) => {
                                return (<button key={i} 
                                            className={ `btn btn-long ${allowNext ? "btn-disabled" : ''} ${item === success ? "btn-success" : ''} ${item === fail ? "btn-danger" : ''}` }
                                            onClick={() => { btnAnswerHandler(item, currentQuiz) }}
                                        >{/*`${item}) `*/} {dic[item][1]}</button>)
                            }) }
                        </div>
                    </>

                ) : ( 

                    <GameResult 
                        wrongAnswers={wrongAnswers}
                        restart={restart}
                        newGame={newGame}
                    />

                ) }
            </div>
            <div className="btn-wrapper">
                
                { quizArray[currentQuiz] || quizArray[currentQuiz] === 0 ? (
                    <button 
                        type="button"
                        className={ `btn btn-long btn-dark ${!allowNext && "btn-disabled"}` }
                        onClick={() => nextQuiz()}
                    >Далее</button>
                ) : "" }
                
            </div>
        </div>
    );

}