import React, { useContext } from 'react'
import Context from '../context.js'

import MenuOrList from './MunuOrList.js'


export default function NavList(props) {

    const {setCurrentComponent, breadcrumbs, setBreadcrumbs} = useContext(Context)

    const buttonList = []

    const offset = props.offset
    const len = props.end - props.start
    const digit = Math.floor(len/offset)
    const rest = len % offset

    // console.log(len, digit, rest)

    if(digit){
        for (let i = 0; i < digit; i++) {
            if(i === digit - 1){
                if(rest >= 9){
                    // console.log('a')
                    buttonList.push(<button 
                        key={i} 
                        className="btn btn-long"
                        onClick={() => { 
                            setCurrentComponent(<MenuOrList start={props.start + i*offset} end={props.start + i*offset + offset - 1} offset={Math.floor(offset/10)} />)
                            setBreadcrumbs([...breadcrumbs, <MenuOrList start={props.start} end={props.end} offset={props.offset} /> ])
                        }}
                        >Слова с {props.start + i*offset + 1} по {props.start + i*offset + offset} {/*(0%)*/}</button>)
                    buttonList.push(<button 
                        key={i+1} 
                        className="btn btn-long"
                        onClick={() => { 
                            setCurrentComponent(<MenuOrList start={props.start + (i+1)*offset} end={props.start + (i+1)*offset + rest} offset={Math.floor(offset/10)} />)
                            setBreadcrumbs([...breadcrumbs, <MenuOrList start={props.start} end={props.end} offset={props.offset} /> ])
                        }}
                        >Слова с {props.start + (i+1)*offset + 1} по {props.start + (i+1)*offset + rest + 1} {/*(0%)*/}</button>)
                }else{
                    // console.log('b')
                    buttonList.push(<button 
                        key={i} 
                        className="btn btn-long"
                        onClick={() => { 
                            setCurrentComponent(<MenuOrList start={props.start + i*offset} end={props.start + i*offset + offset + rest} offset={Math.floor(offset/10)} />)
                            setBreadcrumbs([...breadcrumbs, <MenuOrList start={props.start} end={props.end} offset={props.offset} /> ])
                        }}
                        >Слова с {props.start + i*offset + 1} по {props.start + i*offset + offset + rest + 1} {/*(0%)*/}</button>)
                }
            }else{
                // console.log('c')
                buttonList.push(<button 
                    key={i} 
                    className="btn btn-long"
                    onClick={() => { 
                        setCurrentComponent(<MenuOrList start={props.start + i*offset} end={props.start + i*offset + offset - 1} offset={Math.floor(offset/10)} />)
                        setBreadcrumbs([...breadcrumbs, <MenuOrList start={props.start} end={props.end} offset={props.offset} /> ])
                    }}
                    >Слова с {props.start + i*offset + 1} по {props.start + i*offset + offset} {/*(0%)*/}</button>)
            }
        }
    }else{
        // console.log('d')
        buttonList.push(<button 
            key={0} 
            className="btn btn-long"
            onClick={() => { 
                setCurrentComponent(<MenuOrList start={props.start} end={props.start + rest} offset={Math.floor(offset/10)} />)
                setBreadcrumbs([...breadcrumbs, <MenuOrList start={props.start} end={props.end} offset={props.offset} /> ])
            }}
            >Слова с {props.start + 1} по {props.start + rest + 1} {/*(0%)*/}</button>)
    }

    return (buttonList);

}