import React, { useContext } from 'react'
import Context from '../context.js';

// import {enRu as dic} from '../data/enRu.js'
import { currentDictionary as dic } from '../currentDictionary.js';

import Breadcrumbs from './Breadcrumbs';
import Game from './Game.js';
import Learn from './Learn.js';

export default function WordsList(props) {

    const { setCurrentComponent, answersLength, breadcrumbs, setBreadcrumbs } = useContext(Context)

    const wordsItems = []

    for (let i = props.start, n = 1; i <= props.end; i++, n++) {
        wordsItems.push(<div key={i} className="list-item">{n}) {dic[i][0]} - {dic[i][1]}</div>)
    }

    return (
        <div className="container container-flex list">
            <div className="header">
                <Breadcrumbs />
            </div>
            
            
                <div className="list-body">
                    {/* <div>WordsList! {props.start} - {props.end}</div><br/> */}
                    {wordsItems}
                </div>

                <div className="list-header">
                    <button 
                        className="btn btn-long btn-primary"
                        onClick={() => { 
                            setCurrentComponent(<Game start={props.start} end={props.end} questionLength={props.end - props.start + 1} answersLength={answersLength} />)
                            setBreadcrumbs([...breadcrumbs, <WordsList start={props.start} end={props.end} />])
                        }}
                    >Игра</button>
                    <button 
                    className="btn btn-long"
                    onClick={() => {
                        setCurrentComponent(<Learn start={props.start} end={props.end} wordsLength={props.end - props.start + 1} />)
                        setBreadcrumbs([...breadcrumbs, <WordsList start={props.start} end={props.end} />])
                    }}
                    >Учить</button>
                </div>
            
        </div>
    );

}