import React, {useState, useEffect} from 'react'
import Context from './context.js'

// import {enRu as dic} from './data/enRu.js'
import { lang, title, currentDictionary as dic } from './currentDictionary.js';

import MainMenu from './components/MainMenu.js'

function App() {

  const dictionaryLength = dic.length
  const answersLength = 6

  const [breadcrumbs, setBreadcrumbs] = useState([])

  // console.log(breadcrumbs)

  // let userHistory = localStorage.userHistory ? new Set(JSON.parse(localStorage.userHistory)) : new Set(JSON.parse("[]"))
  let userFavorites = localStorage['userFavorites'+lang] ? new Set(JSON.parse(localStorage['userFavorites'+lang])) : new Set(JSON.parse("[]"))
  // let userMistakes = localStorage.userMistakes ? new Set(JSON.parse(localStorage.userMistakes)) : new Set(JSON.parse("[]"))

  const [currentComponent, setCurrentComponent] = useState(<MainMenu/>)
  const [favorites, setFavorites] = useState(userFavorites)

  useEffect(() => {
      document.title = title
  }, []);

  return (

    <Context.Provider value={{
        dictionaryLength, 
        answersLength, 
        favorites, 
        setFavorites, 
        setCurrentComponent, 
        breadcrumbs, 
        setBreadcrumbs}
    }>
       {currentComponent}
    </Context.Provider>
    
  );
}

export default App
