import React, { useContext } from 'react'
import Context from '../context'

import WordsList from './WordsList';

export default function ButtonAllWords(props) {

    const {setCurrentComponent} = useContext(Context)

    return (
        <>
            { props.end - props.start <= 100 && (
                <button 
                    className="btn btn-long"
                    onClick={() => setCurrentComponent(<WordsList start={props.start} end={props.end} />)}
                >Все слова ({props.end - props.start + 1})</button>
            ) }
        </>
    );

}