import React, { useState, useEffect, useContext, useMemo, useCallback } from 'react'
// import Context from '../context'

// import {enRu as dic} from '../data/enRu.js'
import { currentDictionary as dic } from '../currentDictionary.js';

// import Game from './Game'

export default function GameResult(props) {

    // const { dictionaryLength, answersLength, setCurrentComponent } = useContext(Context)

    return (
        <> 
            <div className="words-list">
            <div className="p">Ошибки:</div>
                { props.wrongAnswers.length > 0 ?props.wrongAnswers.map((item, i) => {
                    return (
                        <div key={i} className="list-item">{dic[item][0]} - {dic[item][1]}</div>
                    )
                }) : <p>ошибок нет</p> }
            </div>

            <div className="btn-list">
                <button className="btn btn-long" onClick={() => props.restart()}>Повторить</button>
                <button className="btn btn-long" onClick={() => props.newGame()}>Новая игра</button>
            </div>
        </>
    )

}