import React from "react"

import WordsList from "./WordsList";
import SubMenu from "./SubMenu";

export default function MenuOrList(props) {

    return (
        <>
        { 
            (props.end - props.start < 20) ? 
                <WordsList start={props.start} end={props.end}/> : 
                <SubMenu start={props.start} end={props.end} offset={props.offset}/> 
        }
        </>
    );

}