import React, { useContext } from 'react'
import Context from '../context'

import { lang, currentDictionary as dic } from '../currentDictionary.js';

import Breadcrumbs from './Breadcrumbs';

export default function Favorites(props) {

    const {favorites, setFavorites, breadcrumbs, setBreadcrumbs} = useContext(Context)

    let favoritesArray = [...favorites]

    const list = []

    function removeFromFavorites(index){
        favoritesArray.splice(index, 1)
        localStorage['userFavorites'+lang] = JSON.stringify([...favoritesArray])
        setFavorites(new Set(favoritesArray))
    }

    for (let i = 0; i < favoritesArray.length; i++) {
        list.push(<div key={i} className="favorites-item"><div><b>{dic[favoritesArray[i]][0]}</b> - {dic[favoritesArray[i]][1]}</div> <button className="btn btn-icon btn-remove" onClick={() => removeFromFavorites(i)}><span className="material-icons">delete</span></button></div>)
    }

    return (
        <div className="container list">
            <div className="header">
                <Breadcrumbs />
            </div>
            <div className="favorites">
                {/* <div className="list-header">
                    <button 
                        className="btn btn-long btn-primary"
                        onClick={() => { 
                            // setCurrentComponent(<Game start={props.start} end={props.end} questionLength={props.end - props.start + 1} answersLength={answersLength} />)
                            // setBreadcrumbs([...breadcrumbs, <Favorites />])
                        }}
                    >Игра</button>
                    <button 
                    className="btn btn-long"
                    onClick={() => {
                        // setCurrentComponent(<Learn start={props.start} end={props.end} wordsLength={props.end - props.start + 1} />)
                        // setBreadcrumbs([...breadcrumbs, <Favorites />])
                    }}
                    >Учить</button>
                </div> */}
                <div className="list-body">
                    {list}
                </div>
            </div>
        </div>
    );

}