import React, { useContext } from 'react'
import Context from '../context.js';

// import {enRu as dic} from '../data/enRu.js'

import { h1 } from '../currentDictionary.js'

import NavList from './NavList';
import Favorites from './Favorites.js';
// import Mistakes from './Mistakes.js'
import Game from './Game.js';
import WordsList from './WordsList.js';
import About from './About.js';

export default function MainMenu(props) {

    const {dictionaryLength, 
        answersLength, 
        favorites, 
        setCurrentComponent, 
        breadcrumbs, setBreadcrumbs} = useContext(Context)

    // const title = `${dictionaryLength} частовстречающихся английских слов, которые покрывают 80% английской речи`

    return (
    <div className="container container-flex list">
        <h1 className="title-main">{ h1 }</h1>
        <div className="list-header">
            <button 
                className="btn btn-long"
                onClick={() => { 
                    setCurrentComponent(<Game start={0} end={dictionaryLength-1} questionLength={10} answersLength={answersLength} />)
                    setBreadcrumbs([...breadcrumbs, <MainMenu />])
                }}
            >Быстрая игра<br/>
                <small>(случайные 10 слов)</small>
            </button>
            {/* <button 
                className="btn btn-long"
            >Игра</button>
            <button 
                className="btn btn-long"
                onClick={() => setCurrentComponent(<Mistakes />)}
            >Мои ошибки (0)</button> */}
            <button 
                className="btn btn-long"
                onClick={() => { 
                    setCurrentComponent(<Favorites />)
                    setBreadcrumbs([...breadcrumbs, <MainMenu />])
                }}
            >Избранное ({favorites.size})</button>
        </div>
        <div className="list-body">
            <NavList start={0} end={dictionaryLength-1} offset={1000} />
        </div>
        <div className="list-footer">
            <button 
                className="btn btn-long"
                onClick={() => { 
                    setCurrentComponent(<WordsList start={0} end={dictionaryLength-1} />)
                    setBreadcrumbs([...breadcrumbs, <MainMenu />])
                }}
            >Все слова ({dictionaryLength})</button>
        </div>
        <div className="footer">
            <a href="#" onClick={(e) => { 
                e.preventDefault()
                setCurrentComponent(<About />)
                setBreadcrumbs([...breadcrumbs, <MainMenu />])
            }}>О программе</a>
        </div>
    </div>
    );

}