import React, { useState, useEffect, useContext, useCallback } from 'react'
import Context from '../context';

import { textToSpeach } from '../functions.js'

// import {enRu as dic} from '../data/enRu.js'
import { lang, languageCode, currentDictionary as dic } from '../currentDictionary.js';

export default function GameQuestion(props) {

    const {favorites, setFavorites} = useContext(Context)
    const [inFavorites, setInFavorites] = useState(favorites.has(props.questionID))
    const [triggerFix, setTriggerFix] = useState(false)
    // console.log(props.questionID, inFavorites, favorites)

    function addToFavorites(id){
        setFavorites(() => {
            if(favorites.has(id)){
                favorites.delete(id)
            }else{
                favorites.add(id)
            }
            localStorage['userFavorites'+lang] = JSON.stringify([...favorites])
            return favorites
        })
        setInFavorites(favorites.has(props.questionID))
    }

    useEffect(() => {
        setInFavorites(favorites.has(props.questionID))
        setTriggerFix(!triggerFix)
    }, [props.questionID])

    return (
        <div className="question">
            <button 
                className="btn btn-icon"
                onClick={() => { addToFavorites(props.questionID) }}
            >
                <span className={`material-icons ${inFavorites && "text-danger"}`}>favorite</span>
            </button>

            <span className="question-text">{ dic[props.questionID][0] }</span>

            {/* <span className="log">{ props.questionID }</span> */}
            
            <button 
                className="btn btn-icon"
                onClick={() => textToSpeach(dic[props.questionID][0], languageCode)}
            >
                <span className="material-icons">volume_up</span>
            </button>
        </div>
    )

}